import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import { Link, useParams } from 'react-router-dom'
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import './Singleview.css'
import ListGroup from 'react-bootstrap/ListGroup';


function Singleview() {

    const params = useParams()
    const [recipe, setRecipe] = useState([])
    const singleData = async () => {
        const { data } = await axios.get(`https://dummyjson.com/recipes/${params.id}`)
        // console.log(data);
        setRecipe(data);
    }
    useEffect(() => { singleData() }, [params])
    // console.log(recipe);
    return (




        <div className=''>

            {recipe.id ?
                <>
                    <Row className=' d-flex justify-content-center ' style={{ fontFamily: "Roboto, sans-serif" }}>

                        <h1 className='text-center mt-5 text-light' style={{ fontSize: '55px' }}><b>{recipe?.name}</b></h1>

                        <Col lg={4} md={4} sm={4}>
                            <h4 className='ms-5 text-success mt-3' >Cuisine :{recipe?.cuisine} </h4>

                        </Col>
                        <Col lg={4} md={4} sm={4}>
                            <h4 className='ms-5 text-success mt-3'>MealType :{recipe?.mealType}</h4>

                        </Col>
                        <Col lg={4} md={4} sm={4}>
                            <h4 className='ms-5 text-success mt-3'>Difficulty :{recipe?.difficulty}</h4>

                        </Col>
                    </Row>


                    <Row className='d-flex  '>

                        <>
                            <Col lg={6} md={6} className=''>
<div className=''>
    
                                    <Card style={{ width: '200rem' }} className='mx-5 w-75 p-3 m-5 mt-5'>
                                        <Card.Body>
                                            <Card.Img className='mt-1 p-3 shadow-lg me-5 card-img' variant="top" src={recipe?.image} />
    
                                        </Card.Body>
                                    </Card>


                                        <Card style={{ width: '200rem' }} className='mx-5 w-75 p-3 m-5 mt-5'>
                                        <Card.Body>
                                        {/* <Card.Title className=' text-danger text-dark'><h4 ><b>Time Required</b></h4></Card.Title> */}
                                        <Card.Subtitle className="mb-2 text-dark mt-2 "><b style={{ fontSize: '15px', color: 'brown' }}>Prep.Time Minutes:</b>  <span style={{ textTransform: 'capitalize', fontSize: '15px' }}>{recipe?.prepTimeMinutes} Minutes</span> </Card.Subtitle>
                                        <Card.Subtitle className="mb-2 text-dark mt-2 "><b style={{ fontSize: '15px', color: 'brown' }}>cook Time Minutes:</b>  <span style={{ textTransform: 'capitalize', fontSize: '15px' }}>{recipe?.cookTimeMinutes} Minutes</span> </Card.Subtitle>

                                        <Card.Text style={{ fontSize: '18px', color: ' #893101' }}>
                                            <b>
                                                "This recipe yields {recipe?.servings} servings, with each serving containing {recipe?.caloriesPerServing} calories."

                                            </b>                            </Card.Text>
    
                                        </Card.Body>
                                    </Card>

</div>                                
                            </Col>




                            <Col lg={6} md={6}>

                                <Card style={{ width: '18rem' }} className='mx-5 w-75 p-3 mt-5 '>
                                    <Card.Body >
                                        <Card.Title className='text-center text-danger'><h1><b>Cooking Guidelines</b></h1></Card.Title>
                                        {/* <Card.Subtitle className="mb-2 text-dark mt-2 "><b style={{ fontSize: '20px' }}>Ingredients:</b> <br /><span style={{ textTransform: 'capitalize', fontSize: '15px' }}>{recipe?.ingredients}</span> </Card.Subtitle>
                                        <Card.Subtitle className="mb-2 text-dark mt-2 "><b style={{ fontSize: '20px' }}>Instructions:</b> <br /><span style={{ textTransform: 'capitalize', fontSize: '15px' }}>{recipe?.instructions}</span> </Card.Subtitle> */}
                                        <h4
                                         className='p-2' style={{color:'black'}}><b>Ingredients</b></h4>
                                        <ol type='1' className='mb-5' style={{color:'black'}}>

                                            <p>{recipe.ingredients && recipe.ingredients.map((ingredient, index) => (
                                                <li key={index}>{ingredient}</li>))}</p>
                                        </ol>
                                        <hr />
                                        <h4 className='p-2 mt-3'style={{color:'black'}}><b>Instructions</b></h4>
                                        <ul style={{color:'black'}}><li><p>{recipe.instructions && recipe.instructions.map((instructions, index) => (
                                            <li key={index}>{instructions}</li>))}</p></li></ul>

                                    </Card.Body>
                                </Card>

                            </Col>


                        </>
                        <div className='d-flex justify-content-between'>
                            <Link to={'/'}><i class="fa-solid fa-circle-arrow-left fs-1 m-3 ms-5"style={{color:'grey'}}></i></Link>
                            <Link to={`/single-view/${Number(params.id) + 1}`}><i class="fa-solid fa-circle-arrow-right fs-1 m-3 me-5"style={{color:'grey'}}></i></Link>
                        </div>



                    </Row>

                </>

                : <div className='text-center p-5 m-5'>
                    <i class="fa-solid fa-spinner fa-spin fa-4x " ></i>

                </div>
            }
        </div>
    )
}

export default Singleview
