import React, { useEffect, useState } from 'react'
import { Col, Row } from 'react-bootstrap'
import './Mainlist.css'
import axios from 'axios'
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import { logDOM } from '@testing-library/react'
import { Link } from 'react-router-dom'
import Aos from 'aos';


function Mainlist() {
  const [recipe,setRecipe]=useState([])
  const [sdata,setSdata]=useState("")

const fetchData=async()=>{
 const {data} =await axios.get('https://dummyjson.com/recipes')
// console.log(data.recipes);
setRecipe(data.recipes)
}

useEffect(()=>{ fetchData()}
,[sdata])
// console.log(recipe);
console.log(sdata);


//Function to search 
const search=()=>{
  const data=recipe.filter(i=>(i.name+i.cuisine).toLowerCase().trim().includes(sdata.toLowerCase().trim()))
// console.log(data);
setRecipe(data);
}
  return (
    <div>
       <Row className=''>

<Col lg={6} className=' p-3'>

  <img src="https://i.postimg.cc/kX6QGNjn/pexels-catscoming-1907227-removebg-preview.png"
    width="375"
    height="350"
    className="d-inline-block align-top  img "

    alt="" />
</Col>

<Col lg={6} className=' mt-5 p-5'>
  <h1>
    <span className='delicious'>
      DELICIOUS

    </span>              <span className='cookbook '> COOKBOOK</span>
  </h1>
  <br />
  <p>
    <span className='text-warning tag'>
      "Indulge Your Culinary Curiosity: Explore Our Recipe Haven!"</span>

  </p>
  <label className='d-flex mt-4 '>
    <input onChange={(e)=>setSdata(e.target.value)} type="text" className='form-control rounded px-3' placeholder='Search reciepe by title or cuisine' />
    <button onClick={search} className='rounded px-3 ' style={{backgroundColor:'white'}}>
      <i class="fa-solid fa-magnifying-glass fa-fade fa-lg"></i>
    </button>

  </label>

</Col>
</Row>
{recipe.length > 0 ?
  <div className='p-5'>
        <Row>
          <div data-aos="fade-right" className='text-center mt-5 '>
            <h1 style={{textTransform:'capitalize',color:'grey',fontSize:'50px'}}>Featured Recipes</h1>
          </div>
        {
          recipe.map(i=>(
  <Col lg={3} md={6} sm={6} className='p-3 d-flex'>
              <Card style={{ width: '28rem' }} className='mt-5 p-3 shadow-lg card'>
              <Card.Img className='mt-1 p-3 shadow-lg me-5 card-img' variant="top" src={i.image}  />
              <Card.Body >
                <Card.Title className='text-center text-danger n1'><h6><b style={{fontSize:'18px'}}>{i.name}</b></h6></Card.Title>
                <Card.Subtitle className='mb-2 mt-2 text-center text-warning'style={{fontSize:'15px'}}><span className='text-success'style={{fontSize:'15px'}}>{i.cuisine}</span> {i.mealType} </Card.Subtitle>
                <Card.Subtitle className='mb-2 mt-5 text-center me-4 'style={{fontSize:'13px',color:'black'}}><b>Time Required:</b> {i.prepTimeMinutes+i.cookTimeMinutes} Min.</Card.Subtitle>

                <Card.Subtitle className='mb-2  text-center 'style={{fontSize:'13px',color:'black'}}><b>Cal. per serving:</b> {i.caloriesPerServing} </Card.Subtitle>
                <Card.Subtitle className='mb-2 mt-2  'style={{fontSize:'13px',color:'black'}}>Rating {i.rating} <i class="fa-solid fa-star fa-fade"style={{ color: '#000000' }} ></i> </Card.Subtitle>
 <Card.Subtitle className='mb-5 mt-2  'style={{color:'black',textTransform: 'lowercase'}}># {i.tags}  </Card.Subtitle>
<Link to={`/single-view/${i.id}`}>
   <Button className='ms-5 mt-5 card-btn' variant="dark">Recipe Details</Button>
  
</Link>               
              </Card.Body>
            </Card>
    
  </Col>        ))
        }
  
  </Row>
  
</div>:        <div className='text-center p-5 m-5 mt-5'>
          <i class="fa-solid fa-spinner fa-spin fa-4x"></i>

        </div>}

    </div>
  )
}

export default Mainlist
